export default class ResultSearch {
    constructor(dataSearch) {
        this.dataSearch = dataSearch;
    }

    async getResultSearch() {
        const { dataSearch } = this;
        let data;
        try {
            let res = await fetch(`https://www.omdbapi.com/?s=${dataSearch}&apikey=aab7b845`);
            data = await res.json();
        } catch (err) {
            document.getElementById('error').textContent = err;
            document.getElementById('error').classList.remove('none');
        }

        let title; let img; let year; let imdbID;
        let isResponse = false;
        let resultSearch = [];

        isResponse = (data.Response).toLowerCase();
        if (isResponse === 'false') { isResponse = false; }

        if (isResponse) {
            data.Search.forEach((element, i) => {
                title = (data.Search[i].Title);
                img = (data.Search[i].Poster);
                year = (data.Search[i].Year);
                imdbID = (data.Search[i].imdbID);
                resultSearch[i] = {
                    title, img, year, imdbID,
                };
            });
        } else {
            resultSearch[0] = {
                title: data.Error, img: './img/noresult.png', year: '', imdbID: '',
            };
        }
        return resultSearch;
    }
}
